<template>
  <header class="header">
    <div class="header-container">
      <!-- ÁΩëÁ´ôÊ†áÈ¢ò -->
      <div class="site-title">
        <h1 class="main-title">ÈùíÂ≤ö</h1>
        <p class="subtitle">ÂØªÈùí‰∫ëÊ∂ßÔºåÊ†ñÂ±±Ê∞¥Èó¥</p>
      </div>
      
      <!-- ÂØºËà™ËèúÂçï -->
      <nav class="navigation">
        <router-link to="/" class="nav-link">È¶ñÈ°µ</router-link>
        <router-link to="/blog" class="nav-link">‰∏™‰∫∫Á¨îËÆ∞</router-link>
        <router-link to="/recommendations" class="nav-link">Êé®Ëçê</router-link>
        <router-link to="/news" class="nav-link">ËµÑËÆØ</router-link>
        <router-link to="/map" class="nav-link">‰∏ñÁïåÂú∞Âõæ</router-link>
      </nav>
      
      <!-- ÊêúÁ¥¢Ê°Ü -->
      <div class="search-box">
        <el-autocomplete
          v-model="searchQuery"
          :fetch-suggestions="querySearchAsync"
          placeholder="Á´ôÂÜÖÊêúÁ¥¢..."
          @select="handleSelect"
          @keyup.enter="handleSearch"
          class="search-input"
          clearable
        >
          <template #suffix>
            <el-icon @click="handleSearch" class="search-icon">
              <Search />
            </el-icon>
          </template>
          <template #default="{ item }">
            <div class="search-suggestion">
              <el-icon class="suggestion-icon">
                <component :is="item.icon" />
              </el-icon>
              <span class="suggestion-text">{{ item.value }}</span>
              <span class="suggestion-type">{{ item.type }}</span>
            </div>
          </template>
        </el-autocomplete>
      </div>
      
      <!-- ÁßªÂä®Á´ØËèúÂçïÊåâÈíÆ -->
      <div class="mobile-menu-btn" @click="toggleMobileMenu">
        <el-icon><Menu /></el-icon>
      </div>
    </div>
    
    <!-- ÁßªÂä®Á´ØËèúÂçï -->
    <div class="mobile-menu" :class="{ active: showMobileMenu }">
      <router-link to="/" class="mobile-nav-link" @click="closeMobileMenu">È¶ñÈ°µ</router-link>
      <router-link to="/blog" class="mobile-nav-link" @click="closeMobileMenu">‰∏™‰∫∫Á¨îËÆ∞</router-link>
      <router-link to="/recommendations" class="mobile-nav-link" @click="closeMobileMenu">Êé®Ëçê</router-link>
      <router-link to="/news" class="mobile-nav-link" @click="closeMobileMenu">ËµÑËÆØ</router-link>
      <router-link to="/map" class="mobile-nav-link" @click="closeMobileMenu">‰∏ñÁïåÂú∞Âõæ</router-link>
    </div>
  </header>
</template>

<script>
import { Search, Menu } from '@element-plus/icons-vue'

export default {
  name: 'HeaderComponent',
  components: {
    Search,
    Menu
  },
  data() {
    return {
      searchQuery: '',
      showMobileMenu: false,
      searchSuggestions: [
        { value: 'Èü≥‰πêÊé®Ëçê', type: 'Êé®Ëçê', icon: 'Headphone' },
        { value: 'ËΩØ‰ª∂Â∑•ÂÖ∑', type: 'Êé®Ëçê', icon: 'Monitor' },
        { value: 'ÁΩëÁ´ôÊî∂Ëóè', type: 'Êé®Ëçê', icon: 'Link' },
        { value: '‰∏™‰∫∫Á¨îËÆ∞', type: 'ÂçöÂÆ¢', icon: 'Document' },
        { value: '‰ªäÊó•Êñ∞È≤ú‰∫ã', type: 'ËµÑËÆØ', icon: 'Notification' },
        { value: 'ÁΩëÂÆâ‰∫ã‰ª∂', type: 'ËµÑËÆØ', icon: 'Lock' },
        { value: 'ÂéÜÂè≤‰∏äÁöÑ‰ªäÂ§©', type: 'ËµÑËÆØ', icon: 'Calendar' },
        { value: 'AIÂ∑•ÂÖ∑', type: 'Êé®Ëçê', icon: 'MagicStick' },
        { value: 'ÁîµÂΩ±Êé®Ëçê', type: 'Êé®Ëçê', icon: 'VideoCamera' }
      ],
      searchHistory: JSON.parse(localStorage.getItem('searchHistory') || '[]')
    }
  },
  methods: {
    handleSearch() {
      if (this.searchQuery.trim()) {
        this.addToSearchHistory(this.searchQuery)
        this.performSearch(this.searchQuery)
      }
    },
    handleSelect(item) {
      this.searchQuery = item.value
      this.handleSearch()
    },
    querySearchAsync(queryString, callback) {
      const suggestions = this.searchSuggestions.filter(item =>
        item.value.toLowerCase().includes(queryString.toLowerCase())
      )

      // Ê∑ªÂä†ÊêúÁ¥¢ÂéÜÂè≤Âà∞Âª∫ËÆÆ‰∏≠
      const historyItems = this.searchHistory
        .filter(item => item.toLowerCase().includes(queryString.toLowerCase()))
        .slice(0, 3)
        .map(item => ({ value: item, type: 'ÂéÜÂè≤', icon: 'Clock' }))

      const allSuggestions = [...historyItems, ...suggestions].slice(0, 8)
      callback(allSuggestions)
    },
    addToSearchHistory(query) {
      const history = this.searchHistory.filter(item => item !== query)
      history.unshift(query)
      this.searchHistory = history.slice(0, 10) // ‰øùÁïôÊúÄËøë10Êù°
      localStorage.setItem('searchHistory', JSON.stringify(this.searchHistory))
    },
    performSearch(query) {
      // Ê†πÊçÆÊêúÁ¥¢ÂÜÖÂÆπË∑≥ËΩ¨Âà∞Áõ∏Â∫îÈ°µÈù¢
      const lowerQuery = query.toLowerCase()

      if (lowerQuery.includes('Èü≥‰πê') || lowerQuery.includes('Ê≠åÊõ≤')) {
        this.$router.push({ path: '/recommendations', query: { category: 'music' } })
      } else if (lowerQuery.includes('ËΩØ‰ª∂') || lowerQuery.includes('Â∑•ÂÖ∑')) {
        this.$router.push({ path: '/recommendations', query: { category: 'software' } })
      } else if (lowerQuery.includes('ÁΩëÁ´ô') || lowerQuery.includes('ÈìæÊé•')) {
        this.$router.push({ path: '/recommendations', query: { category: 'websites' } })
      } else if (lowerQuery.includes('Á¨îËÆ∞') || lowerQuery.includes('ÂçöÂÆ¢') || lowerQuery.includes('ÊñáÁ´†')) {
        this.$router.push('/blog')
      } else if (lowerQuery.includes('Êñ∞Èóª') || lowerQuery.includes('ËµÑËÆØ') || lowerQuery.includes('‰∫ã‰ª∂')) {
        this.$router.push('/news')
      } else if (lowerQuery.includes('Âú∞Âõæ')) {
        this.$router.push('/map')
      } else if (lowerQuery.includes('ai') || lowerQuery.includes('‰∫∫Â∑•Êô∫ËÉΩ')) {
        this.$router.push({ path: '/recommendations', query: { category: 'ai' } })
      } else if (lowerQuery.includes('ÁîµÂΩ±') || lowerQuery.includes('ÁîµËßÜ')) {
        this.$router.push({ path: '/recommendations', query: { category: 'movies' } })
      } else {
        // ÈªòËÆ§ÊêúÁ¥¢ÔºåÂèØ‰ª•Âú®ËøôÈáåÂÆûÁé∞ÂÖ®ÊñáÊêúÁ¥¢
        this.$router.push({ path: '/search', query: { q: query } })
      }

      this.$emit('search', query)
    },
    toggleMobileMenu() {
      this.showMobileMenu = !this.showMobileMenu
    },
    closeMobileMenu() {
      this.showMobileMenu = false
    }
  }
}
</script>

